import React from 'react'
import { Link, useParams } from 'react-router-dom';
import TwitterIcon from '@mui/icons-material/Twitter';
import LanguageIcon from '@mui/icons-material/Language';
import RedditIcon from '@mui/icons-material/Reddit';
import GitHubIcon from '@mui/icons-material/GitHub';
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import YouTubeIcon from '@mui/icons-material/YouTube';

function getSigIdShort(vc) {
  const id = vc.id;
  // get everything after the last slash
  const sigIdShort = id.substring(id.lastIndexOf('/') + 1);
  return sigIdShort;
}

function getSigIdShorts(scvp) {
  return scvp['verifiableCredential'].map(getSigIdShort);
}

function getServiceIcon(service) {
  switch (service.toLowerCase()) {
    case 'twitter':
      return <TwitterIcon />;
    case 'reddit':
      return <RedditIcon />;
    case 'github':
      return <GitHubIcon />;
    case 'facebook':
      return <FacebookIcon />;
    case 'instagram':
      return <InstagramIcon />;
    case 'linkedin':
      return <LinkedInIcon />;
    case 'youtube':
      return <YouTubeIcon />;
    default:
      return <LanguageIcon />;
  }
}


function Home() {

  const { id } = useParams();
  const scvp = require('../resources/json/scvp.json')
  const did = require('../resources/json/did.json')


  const name = did.subject.displayName;
  const image = did.subject.displayImg;
  const type = did.subject.type;
  // const [link, setLink] = React.useState(null);
  // const [linkVC, setLinkVC] = React.useState(null);

  const [provenVC, setProvenVC] = React.useState(null);

  React.useEffect(() => {
    console.log("id: " + id);


    setProvenVC(scvp.verifiableCredential.find(vc => vc.id.substring(vc.id.lastIndexOf('/') + 1) === id) || undefined);
    console.log("provenVC: " + JSON.stringify(provenVC, null, 2));
  }, [id, scvp.verifiableCredential])

  const getServiceVCs = () => {
    const serviceVCs = [];
    for (const vc of scvp.verifiableCredential) {
      if (vc.credentialSubject.type === "identity") {
        serviceVCs.push(vc);
      }
    }
    return serviceVCs;
  }

  return (
    <div className="mx-auto max-w-7xl px-4 py-16 sm:px-6 lg:px-8">
      <div className="">
        {/* Invoice */}

        <div className="-mx-4 px-4 py-8 shadow-sm ring-1 ring-gray-900/5 sm:mx-0 sm:rounded-lg sm:px-8 sm:pb-14 lg:col-span-2 lg:row-span-2 lg:row-end-2 xl:px-16 xl:pb-20 xl:pt-16">
          <div className="flex justify-center">
            <div className="border-orange-100">
              <div className="flex justify-end">
                <Link to={`/sigchain/${getSigIdShort(scvp.verifiableCredential[0])}`} className='flex items-center'>
                  <h5 className="text-sm font-medium leading-6 text-gray-900">Sigchain</h5>
                  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-4 h-4">
                    <path strokeLinecap="round" strokeLinejoin="round" d="M8.25 4.5l7.5 7.5-7.5 7.5" />
                  </svg>
                </Link>
              </div>
              <div className="flex">
                <div className=" sm:px-0 justify-end">
                  <div className="flex-1">
                    <div className="text-center content-center items-center">
                      <div className="flex justify-center">
                        <img className="h-12 w-12 rounded-full " src={image} alt="" />
                      </div>
                      <h3 className="text-base font-semibold leading-7 text-gray-900 mt-2">{name}</h3>
                      <p className="mt-1 max-w-2xl text-sm leading-6 text-gray-500">{type === 'person' ? "Individual" : "Organization"}</p>
                    </div>
                  </div>
                </div>
                <div className="flex-1 border-gray-100">
                  <dl className="">
                    {getServiceVCs().map((vc, index) => {
                      const service = vc.credentialSubject.service;
                      const username = vc.credentialSubject.service.username;
                      const serviceId = vc.id.substring(vc.id.lastIndexOf('/') + 1);
                      if (serviceId === id) {
                        return (
                          <div className="bg-green-200 px-4 py-6 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-3">
                            <a href={service.user} className="mt-1 text-sm leading-6 text-blue-600 dark:text-blue-500 hover:underline  sm:mt-0">{service.username}</a>
                            <a href={service.proof} className="mt-1 text-sm leading-6 text-blue-600 dark:text-blue-500 hover:underline  sm:mt-0">{service.proof}</a>
                          </div>
                        )
                      }

                      return (
                        <div className="mx-3">
                          {getServiceIcon(service.name)}
                          <a href={service.user} className="mt-1 mx-1 text-lg leading-6 text-blue-600 dark:text-blue-500 hover:underline  sm:mt-0">{service.username}</a>
                          <a href={service.proof} className="mt-1 text-xs leading-6 align-top text-sky-300 dark:text-blue-500 hover:underline  sm:mt-0">proof</a>
                        </div>
                      )
                    }
                    )}
                  </dl>
                </div>
              </div>
              {id != null ?
                provenVC != null ?
                  provenVC.credentialSubject.type === 'identity' ?
                    <div className="mx-auto grid max-w-2xl grid-cols-1 grid-rows-1 items-start gap-x-8 gap-y-8 lg:mx-0 lg:max-w-none bg-green-500 mt-10">
                      <h3 className="text-base font-semibold leading-7 text-gray-900">It is proven!</h3>
                    </div> :
                    <div className="mx-auto grid max-w-2xl grid-cols-1 grid-rows-1 items-start gap-x-8 gap-y-8 lg:mx-0 lg:max-w-none bg-red-500 mt-10">
                      <h3 className="text-base font-semibold leading-7 text-gray-900">The given link is not a proven identity.</h3>
                    </div>
                  : <></>
                : <></>
              }
            </div>
          </div>
        </div>

      </div>


    </div>
  )
}


export default Home;